import React, { useState } from "react"
import PropTypes from "prop-types"

import Header from "./header"
import NavButtons from "./navButtons"
import SegmentTitles from '../../data/SegmentTitles.json'
import AvailableContent from '../../data/availableContent.json'
import "./layout.module.css"
import Menu from '../Menu/Menu'

import AppContext from '../../contextAPI/AppContext'
import WordDataTable from '../WordDataTable/WordDataTable'
import SearchPanel from '../SearchPanel/SearchPanel'
import VerbView from '../VerbView/VerbView'

import SourceViewInfo from '../SourceViewInfo/SourceViewInfo'

import { ThemeProvider } from '@material-ui/styles';
import theme from '../../theme'
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Container from '@material-ui/core/Container';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';

const useStyles = makeStyles({
  textBody: {
    margin: '70px 8px 8px 8px'
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
  dialog: {
    width: '75vw',
    height: '75vh'
  },
  definition: {
    fontWeight: 'bold'
  }
});

const WrapLayout = ({ children, location, pageContext: {segmentID, language, version} }) => {
  const classes = useStyles();

  // This set of code sets the links for the previous and next buttons of the navigation
  const segmentIDs = Object.keys(SegmentTitles)
  let next
  let prev
  const currentSegment = location.pathname.split('/')[location.pathname.split('/').length -1]
  if (location.pathname !== '/') {
    const index = segmentIDs.indexOf(segmentID)
    next = (AvailableContent[language][version][segmentIDs[index + 1]] !== undefined) ? `/${language}/${version}/${segmentIDs[index + 1]}` : `/`
    prev = (AvailableContent[language][version][segmentIDs[index - 1]] !== undefined) ? `/${language}/${version}/${segmentIDs[index - 1]}` : `/`
  }

  // -------------------------------------------------- //
  // -------------- STATE OF THE WEB APP -------------- //
  // -------------------------------------------------- //
  const [ currentVersion, updateVersion ] = useState((version !== undefined) ? version : 'NLT')
  const [ currentLanguage, updateLanguage ] = useState((language !== undefined) ? language : 'ENG')
  const [ currentSegmentID, updateSegmentID ] = useState((segmentID !== undefined) ? segmentID : 'I042')
  const [ languageSelect, updateLanguageSelect ] = useState(false)
  const [ versionSelect, updateVersionSelect ] = useState(false)
  const [ currentSegmentPage, updateCurrentSegmentPage ] = useState(currentSegment)
  // This controls the popup for the word information
  const [ dialog, updateDialog ] = useState({open: false, content: {}})
  const newDialog = (obj) => {
    updateDialog(obj)
  }
  // This controls the popup for the source information
  const [ sourceDialog, updateSourceDialog ] = useState({open: false, content: {}})
  const newSourceDialog = (obj) => {
    updateSourceDialog(obj)
  }

  const [ menu, updateMenu ] = useState(false)
  const [ searchMenu, updateSearchMenu ] = useState(false)
  const [ searchID, updateSearchID ] = useState('')
  
  // keep DisplayRef user setting when changing pages
  let currentDisplayRef = false
  if (location) {
    if (location.state !== null && location.state !== undefined){
      if (location.state.currentDisplayRef !== undefined) {
        currentDisplayRef = location.state.currentDisplayRef
      }
    }
  }
  const [ displayRef, updateDisplayRef ] = useState(currentDisplayRef)

  // keep interlinear user setting when changing pages
  let currentInterlinear = false
  if (location) {
    if (location.state !== null && location.state !== undefined){
      if (location.state.currentInterlinear !== undefined) {
        currentInterlinear = location.state.currentInterlinear
      }
    }
  }
  const [ interlinear, updateInterlinear ] = useState(currentInterlinear)  
  // -------------------------------------------------- //
  // ------------------- END OF STATE ----------------- //
  // -------------------------------------------------- //


  const dictionary = (version === 'GNA') ? require('../../data/strongsGreekDictionary.json') : (version === 'WLC') ? require('../../data/strongsHebrewDictionary.json') : null
  return (
    <ThemeProvider theme={theme}>
      <AppContext.Provider value={{
          menu: menu,
          updateMenu: () => updateMenu(!menu),
          searchMenu: searchMenu,
          updateSearchMenu: () => updateSearchMenu(!searchMenu),
          dialog: dialog, 
          newDialog: newDialog, 
          displayRef: displayRef, 
          updateDisplayRef: updateDisplayRef,
          interlinear: interlinear,
          updateInterlinear: updateInterlinear,
          version: currentVersion,
          updateVersion: updateVersion,
          language: currentLanguage,
          updateLanguage: updateLanguage,
          segmentID: currentSegmentID,
          updateSegmentID: updateSegmentID,
          searchID: searchID,
          updateSearchID: updateSearchID,
          languageSelect: languageSelect,
          updateLanguageSelect: () => updateLanguageSelect(!languageSelect),
          versionSelect: versionSelect,
          updateVersionSelect: () => updateVersionSelect(!versionSelect),
          sourceDialog: sourceDialog,
          updateSourceDialog: newSourceDialog,
          currentSegmentPage: currentSegmentPage,
          updateCurrentSegmentPage: updateCurrentSegmentPage
        }}
          >
          <Drawer 
            className={classes.drawer}
            open={menu} 
            anchor="left"
            width="75%"
            onClose={() => updateMenu(false)}>
              <Menu location={location}/>
          </Drawer>
          <Drawer 
            className={classes.drawer}
            open={searchMenu} 
            anchor="right"
            width="75%"
            onClose={() => updateSearchMenu(false)}>
              <SearchPanel />
          </Drawer>
          <Header 
            siteTitle={'SourceView Reader'} 
            interlinear={interlinear}
            version={version}
            showIcons={(location.pathname !== '/') ? true : false}
          />
          <Container>
            <main className={classes.textBody}>{children}</main>
            {(dialog.open === true) ? (
                  <Dialog 
                    maxWidth='lg'
                    open={dialog.open} 
                    onClose={() => newDialog({open: false, content: {}})}>
                      <DialogTitle>{`${dialog.content.text} : ${(dialog.content.StrongNumber !== '') ? dialog.content.StrongNumber : dialog.content.extendedStrongNumber}`}</DialogTitle>
                      <DialogContent dividers>
                        <p className={classes.definition}>{`DEFINITION: `}<em>{(dictionary[dialog.content.StrongNumber]) ? dictionary[dialog.content.StrongNumber].strongs_def : 'N/A'}</em></p>
                        <WordDataTable />
                        {(dialog.content.StrongNumber === 'H4487') ? (<VerbView />) : null}
                      </DialogContent>  
                  </Dialog> 
              ) : null}
            {(sourceDialog.open === true) ? (
                  <Dialog 
                    maxWidth='lg'
                    open={sourceDialog.open}
                    onClose={() => newSourceDialog({open: false, content: {}})}>
                      <DialogTitle>SourceView Info</DialogTitle>
                      <DialogContent dividers>
                        <SourceViewInfo />  
                      </DialogContent>
                  </Dialog> 
              ) : null}
          </Container>
          {(location.pathname !== '/') ? <NavButtons 
          next={next} 
          prev={prev}
          /> : null}
        </AppContext.Provider>
      </ThemeProvider>
    )}

WrapLayout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default WrapLayout
