import React, { useContext } from 'react'
import LanguageVersionInfo from '../../data/LangsVersions.json'
import AvailableContent from '../../data/availableContent.json'
import {Dropdown} from 'primereact/dropdown';
import AppContext from '../../contextAPI/AppContext'
import styles from './Menu.module.css'
import { navigate } from "@reach/router";
import { StaticQuery, graphql } from 'gatsby'

import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import InboxIcon from '@material-ui/icons/Inbox';
import Home from '@material-ui/icons/Home';
import Avatar from '@material-ui/core/Avatar';
import LanguageSelect from '../AutoComplete/LanguageSelect'
import VersionSelect from '../AutoComplete/VersionSelect'
import SVReaderLogo from '../../images/SVLogo.jpeg'

const useStyles = makeStyles(theme => ({
    root: {
      width: '100%',
      maxWidth: 360,
      backgroundColor: theme.palette.background.paper,
    },
    avatar: {
        marginRight: 10,
      },
  }));

function ListItemLink(props) {
return <ListItem button component="a" {...props} />;
}

const Menu = (props) => {
    const classes = useStyles();
    const receivedAppContext = useContext(AppContext)
    
    const getLangOptions = () => {
        let LanguageOptions = []
        const languageKeys = Object.keys(LanguageVersionInfo)
        languageKeys.forEach(key => {
            const langObj = {
                label: LanguageVersionInfo[key].language_name,
                value: key
            }
            LanguageOptions.push(langObj)
        })
        return LanguageOptions
    }

    const getVersionOptions = (langKey) => {
        let VersionOptions = []
        const versionKeys = Object.keys(LanguageVersionInfo[langKey].versions)
        versionKeys.forEach(key => {
            const versionObj = {
                label: LanguageVersionInfo[langKey].versions[key],
                value: key
            }
            VersionOptions.push(versionObj)
        })
        return VersionOptions
    }

    const updateLang = (event, paths) => {
        receivedAppContext.updateLanguage(event.value)
        receivedAppContext.updateVersion(getVersionOptions(event.value)[0].value)
        if (getVersionOptions(event.value).length < 2) {
            receivedAppContext.updateLanguageSelect(false)
            if (props.location.pathname !== '/') {
                receivedAppContext.updateInterlinear(false)
                const firstBookOfNewLanguage = Object.keys(AvailableContent[event.value][getVersionOptions(event.value)[0].value])[0]
                const firstSegmentOfNewLanguage = AvailableContent[event.value][getVersionOptions(event.value)[0].value][firstBookOfNewLanguage].segments[1]
                const path = (paths.find(node => node.path === `/${event.value}/${getVersionOptions(event.value)[0].value}/${firstSegmentOfNewLanguage}`) ) ? console.log('path matches')
                // `/${event.value}/${getVersionOptions(event.value)[0].value}/${receivedAppContext.segmentID}` 
                : `/${event.value}/${getVersionOptions(event.value)[0].value}/${firstSegmentOfNewLanguage}`
                navigate(path)
            }
        } else {
            revealVersionSelect()
        }
    }

    const updateVersion = (event, paths) => {
        receivedAppContext.updateVersion(event.value)
        receivedAppContext.updateVersionSelect(false)
        if (props.location.pathname !== '/') {
            receivedAppContext.updateInterlinear(false)
            const firstBookOfNewLanguage = Object.keys(AvailableContent[receivedAppContext.language][event.value])[0]
            const firstSegmentOfNewLanguage = AvailableContent[receivedAppContext.language][event.value][firstBookOfNewLanguage].segments[1]
            const path = (AvailableContent[receivedAppContext.language][event.value][firstBookOfNewLanguage].segments.indexOf(receivedAppContext.segmentID) !== -1) ? `/${receivedAppContext.language}/${event.value}/${receivedAppContext.segmentID}` : `/${receivedAppContext.language}/${event.value}/${firstSegmentOfNewLanguage}`
            navigate(path)
        }
    }

    const revealLanguageSelect = () => {
        receivedAppContext.updateLanguageSelect(true)
    }

    const revealVersionSelect = () => {
        receivedAppContext.updateLanguageSelect(false)
        receivedAppContext.updateVersionSelect(true)
    }

    return (
        <div className={classes.root}>
            <List component="nav" aria-label="Main mailbox folders">
                <ListItem onClick={() => {
                        receivedAppContext.updateMenu()
                        navigate('/')
                    }} >
                    <Avatar alt="Remy Sharp" src={SVReaderLogo} className={classes.avatar} />
                    <ListItemText primary="SourceView Bible Reader" />
                </ListItem>
                {/* <ListItem button>
                    <ListItemIcon>
                        <Home />
                    </ListItemIcon>
                    <ListItemText primary="HOME" />
                </ListItem> */}
                <Divider />
                <ListItem>
                    <LanguageSelect location={props.location} />
                </ListItem>
                <ListItem>
                    <VersionSelect location={props.location} />
                </ListItem>
            </List>
            {/* <Divider />
            <List component="nav" aria-label="Secondary mailbox folders">
                <ListItem button>
                <ListItemText primary="Trash" />
                </ListItem>
                <ListItemLink href="#simple-list">
                <ListItemText primary="Spam" />
                </ListItemLink>
            </List> */}
        </div>)
}

export default Menu;